<template>
  <Layout class="problem-detail-page">
    <Row class="tool-bar">
      <a href="javascript: void(0)" @click="$router.go(-1)">
        <icon type="ios-arrow-back" />
        返回
      </a>

      <div class="buttons">
        <Button type="primary" size="small" @click.native="saveProblem">保存</Button>
      </div>

    </Row>
    <Form class="problem-form" :label-width="36">
      <FormItem :model="problemData" label="分类">
        <professionalSelector :professionalId="problemData.professionId" @on-select="problemData.professionId = $event.id; problemData.tagInfo = []" />
      </FormItem>
      <FormItem label="标题">
        <Input v-model="problemData.title"/>
      </FormItem>
      <FormItem label="标签">
        <tagSelector v-model="problemData.tagInfo" :professionalId="problemData.professionId"/>
      </FormItem>
      <mavon-editor
        class="md-editor"
        v-model="problemData.content"
        :scrollStyle="true"
        placeholder="请输入内容"
        @fullScreen="toggleFullScreen"
        @change="contentChange"
      />
    </Form>
  </Layout>
</template>

<script>
import professionalSelector from '@/components/professional-selector'
import tagSelector from '@/components/tag-selector'
import { saveProblem, getProblemDetail } from '@/api/problem'

export default {
  name: 'problem-detail',
  components: {
    professionalSelector,
    tagSelector
  },
  data () {
    return {
      fullScreen: false,
      problemData: {
        title: '',
        sort: 0,
        content: '',
        tagId: [],
        professionId: 0,
        tagInfo: [],
        range: 1,
        type: 1
      }
    }
  },
  created () {
    if (this.$route.params.type === 'detail') {
      getProblemDetail({ id: this.$route.params.id }).then(resp => {
        if (resp.response.success) {
          resp.data.tagInfo = resp.data.tagInfo ? resp.data.tagInfo : []
          resp.data.professionId = resp.data.professionId ? resp.data.professionId : 0
          this.problemData = resp.data
        }
      })
    }
  },
  methods: {
    toggleFullScreen (params) {
      this.$store.commit('layout/toggleFullScreen', false)
    },
    contentChange (value, html) {
      this.problemData.content = value
    },
    saveProblem () {
      saveProblem(this.problemData).then(resp => {
        if (resp.response.success) {
          this.$Message.success('保存成功！')
          this.$router.replace({ name: 'Problem' })
        }
      })
    }
  }
}
</script>

<style scoped lang="stylus">
.problem-detail-page
  background none
  height 100%
  padding 0 0.5rem
  .tool-bar
    display flex
    margin-bottom 0.5rem
    .buttons
      flex 1 1 auto
      text-align right
  .problem-form
    height 100%
    display flex
    flex-direction column
    .md-editor
      flex 1 1 auto
</style>
